* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}


img {
  max-width: 30px;
  margin-right: 15px;

}

.country-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
  list-style: none;
}

.country-info span {
  font-weight: bold;
}

.name {
  font-size: 35px;
  font-weight: bold;
  line-height: 1;
}

.title {
  display: flex;
  align-items: center;
}